.t_container {
    padding-left: calc(2 * 0.75rem);
    padding-right: calc(2 * 0.75rem);
    flex-wrap: wrap;
    display: block;
}

.t_container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 650px) {
    .t_container {
        max-width: 650px;
    }
}

@media (min-width: 960px) {
    .t_container {
        max-width: 960px;
    }
}

@media (min-width: 1280px) {
    .t_container {
        max-width: 1280px;
    }
}

@media (min-width: 1400px) {
    .t_container {
        max-width: 1400px;
    }
}

@media (min-width: 1921px) {
    .t_container {
    max-width: 1920px;
    }
}






